@font-face {
   font-family: hanae;
   src: url("hanae/MrsEavesPetiteCaps.woff2");
}

@font-face {
  font-family: hanae_ital;
  src: url("hanae_ital/MrsEaves-Italic.woff2");
}

@font-face {
  font-family: hanae_humble;
  src: url("humble/Humble.woff2");
}



* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'hanae', sans-serif;
}


.home,
.services,
.products,
.sign-up {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.services {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.products {
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.sign-up {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}
